@import url(https://fonts.googleapis.com/css2?family=Cairo:wght@300;400;600;700&display=swap);
html {
  direction: rtl;
}

body {
  margin: 0;
  font-family: "Cairo", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h6 {
  font-weight: 600;
  color: #323232;
}
p {
  margin-bottom: 0;
}
.center {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.ShippingAddress .address-details {
  color: #404559;
  background: #f9f9f9;
  border: 2px solid #f1f1f1;
  border-radius: 5px;
  padding: 15px;
  font-size: 0.85rem;
}
.Complaint {
  border: 2px solid #f1f1f1;
  border-radius: 5px;
  padding: 15px;
  font-size: 0.75rem;
  font-weight: 700;
  text-align: center;
}
.Complaint img {
  width: 100%;
  height: 100%;
}
@media screen and (max-width: 767px) {
  .Complaint img {
    height: 60%;
    width: 60%;
  }
}
.Complaint p {
  margin-bottom: 10px;
}
.Complaint button {
  background-color: #ff0000;
  color: #fff;
  font-weight: 700;
  padding: 7px 42px;
  border-radius: 10px;
  font-size: 0.7rem;
}
@media screen and (min-width: 768px) and (max-width: 991px) {
  .Complaint button {
    padding: 7px 17px;
  }
}

.ShipmentProgress .card-body {
  padding: 25px;
}

.ShipmentProgress .details p {
  font-weight: 700;
}
.ShipmentProgress .details h6 {
  color: #999999;
}

.ShipmentProgress hr {
  margin: 20px -20px 0;
}
.ShipmentProgress .progress-step {
  border: 1px solid black;
  border-radius: 50%;
}

.Stepper {
  margin-top: auto;
  display: flex;
  justify-content: space-between;
  margin: 30px 0 5px;
}

.stepper-item {
  font-size: 13px;
  font-weight: 700;
  color: #d1d1d1;
  text-align: center;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 1 1;
}
@media (max-width: 768px) {
  .stepper-item {
    font-size: 13px;
  }
}

/* uncompleted step bar styles */
.stepper-item::before {
  position: absolute;
  content: attr(data-before);
  border-bottom: 7px solid #e9e9e9;
  width: 100%;
  top: 17px;
  left: -50%;
  z-index: 2;
}
.stepper-item:last-child::before {
  content: none;
}

/* completed step bar styles */
.stepper-item.completed:not(:first-child) .progress-bar {
  position: absolute;
  content: attr(data-after);
  border-bottom: 7px solid #f6ad0a;
  width: 100%;
  top: 17px;
  left: 50%;
  z-index: 3;
}

/* delivered/cancelled step bar styles */
.stepper-item.completed:not(:first-child) .progress-bar.delivered {
  border-color: #1ac45e;
}
.stepper-item.completed .step-counter.delivered {
  background-color: #1ac45e;
}
.stepper-item.completed:not(:first-child) .progress-bar.cancelled {
  border-color: #f00;
}
.stepper-item.completed .step-counter.cancelled {
  background-color: #f00;
}

/* step checkpoint styles */
.stepper-item .step-counter {
  font-size: 18px;
  position: relative;
  z-index: 5;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  border: 2px solid #d1d1d1;
  border-radius: 50%;
  background-color: #fff;
  margin-bottom: 6px;
}
.stepper-item:last-child .step-counter {
  font-size: 22px;
}
.stepper-item.completed .step-counter {
  font-size: 12px;
  background-color: #f6ad0a;
  border: none;
  color: #fff;
  width: 22px;
  height: 22px;
  top: 9px;
}
.stepper-item.current-state .step-counter {
  font-size: 18px;
  width: 40px;
  height: 40px;
  top: 0;
}
.stepper-item.current-state:last-child .step-counter {
  font-size: 12px;
  width: 22px;
  height: 22px;
  top: 9px;
}

/* step name styles */
.stepper-item.completed .step-name {
  color: #323232;
  position: relative;
  top: 18px;
}
.stepper-item.completed p {
  margin-top: 2px;
}
.stepper-item.current-state .step-name {
  top: 0;
}
.stepper-item.current-state:last-child .step-name {
  top: 18px;
}

.ShipmentTransitDetails {
  overflow-x: auto;
}
table {
  width: 100%;
  font-size: 0.85rem;
  border-collapse: separate;
  border-spacing: 0;
}
/* table's top border */
table th {
  color: #8f9bb2;
  background: #f9f9f9;
  border-top: 2px solid #f1f1f1;
}
table td {
  font-weight: 600;
  color: #404559;
}
/* table's and cells' bottom border */
table th,
table td {
  border-bottom: 2px solid #f1f1f1;
  padding: 15px;
}
@media screen and (max-width: 480px) {
  table th,
  table td {
    padding: 13px 5px;
    text-align: center;
  }
}
/* table's right border */
table th:first-child,
table td:first-child {
  border-right: 2px solid #f1f1f1;
}
/* table's right border */
table th:last-child,
table td:last-child {
  border-left: 2px solid #f1f1f1;
}
/* table's corners */
table tr:first-child th:first-child {
  border-top-right-radius: 5px;
}
table tr:first-child th:last-child {
  border-top-left-radius: 5px;
}
table tr:last-child td:first-child {
  border-bottom-right-radius: 5px;
}
table tr:last-child td:last-child {
  border-bottom-left-radius: 5px;
}

#date-fix {
  text-align: right;
  direction: ltr;
}

.ShipmentInqueryForm {
  box-shadow: 0 3px 12px #dedede;
  background-color: #fff;
  width: 410px;
  border: 10px;
  border-radius: 10px;
  padding: 40px;
  color: #5e5e5e;
  text-align: right;
}
.ShipmentInqueryForm form {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-right: 0;
}
.ShipmentInqueryForm h4 {
  font-weight: 600;
  color: #f00;
  margin-bottom: 12px;
}
.ShipmentInqueryForm p {
  font-size: 18px;
  margin-bottom: 25px;
}

.ShipmentInqueryForm form input {
  border: 1px solid #e3e7ec;
  border-radius: 5px;
  width: 80%;
  padding: 5px 10px;
}
.ShipmentInqueryForm form button {
  background-color: #fb0006;
  color: white;
  border: none;
  border-radius: 50%;
  font-size: 14px;
  height: 32px;
  width: 32px;
  margin-left: 5px;
  padding: 0;
}

@media screen and (max-width: 768px) {
  .ShipmentInqueryForm {
    width: 400px;
  }
}

@media screen and (max-width: 480px) {
  .ShipmentInqueryForm {
    width: 300px;
    padding: 30px;
  }
  .ShipmentInqueryForm form input {
    width: 75%;
  }
  .ShipmentInqueryForm p {
    margin-bottom: 15px;
  }
}

.navbar {
  padding: 15px 0;
  border-bottom: 1px solid #dee2e6;
}
.navbar .navbar-brand img {
  width: 115px;
  height: 35px;
  margin-left: 40px;
}
.navbar .navbar-nav {
  margin-right: 25px;
}
.navbar .navbar-nav .nav-link {
  padding: 0 25px;
  color: #323232;
  font-weight: 700;
}
.navbar .navbar-nav .nav-link:hover {
  color: #fb0006;
  cursor: pointer;
}
.navbar .dropdown-menu {
  border: none;
  background-color: inherit;
}
.navbar .dropdown-menu .ShipmentInqueryForm {
  background-color: #fafafa;
}

@media screen and (max-width: 768px) {
  .left-nav {
    margin-right: 25px !important;
  }
  .navbar-nav .nav-link {
    margin-top: 10px;
  }
}

